@font-face {
    font-family: 'block';
    src: local('block'), url(./assets/fonts/block.ttf) format('truetype');
  }

  @font-face {
    font-family: 'BebasNeue';
    src: local('BebasNeue'), url(./assets/fonts/BebasNeue-Regular.ttf) format('truetype');
  }

.artist h2{
    padding-top: 30px;
    font-family: 'block';
}
.artist a{
    font-family: 'BebasNeue';   
  }
.artist a:link {
    color: rgb(50, 50, 50);
    text-decoration: none;
  }
  
  /* visited link */
.artist a:visited {
    color: black;
    text-decoration: none;
  }
  
  /* mouse over link */
.artist a:hover {
    color: maroon;
    font-weight: bold;
    text-decoration: none;
  }
  
  /* selected link */
.artist a:active {
    color: black;
    text-decoration: none;
  }


 .timer{
    display:inline-flex;
    font-family: 'block';
    color: #fff;
    background-color: #000;
    font-size: 34px;
    line-height: 1;
    padding: 8px 8px 8px 8px;
    margin-right:20px;
} 


.f-container h1{
    font-family: 'block';
    display: inline-flex;
}
  
.img{
width:150px;
}
  
.imgHeader{
    width:250px;
}


html,
body{
  margin: 0;
  padding: 0;
}
.f-container{
  height: 100vh; /* for shorter content */
  display: flex;
  flex-direction: column;
    
  /*ie10*/
  display: -ms-flexbox;
  -ms-flex-direction: column;
}

.main{
  flex: 1;
  
  /*ie10*/
  -ms-flex: 1 1 auto; /*needs both shrink and grow*/
}

header,
footer{
  flex-basis: 50px;
  
  /*ie10*/
  -ms-flex-preferred-size: 50px;
  
  /*center align of links*/
  display: flex;
  align-items: center; /*vertical*/
  justify-content: space-around; /*or space-between*/
  /*ie10*/
  display: -ms-flexbox;
  -ms-flex-align: center; /*vertical*/
  -ms-flex-line-pack: distribute; /*or justify*/
}

footer {
  /*ie10 short content*/
  margin-top: auto; 
}
header a{
  display: inline-block;
}
.fixed-hf .main{
  overflow-y: auto;
}
@media (max-width: 1025px){
    .f-container{
        height: 100vh; /* for shorter content */
        display: flex;
        flex-direction: column;
          
        /*ie10*/
        display: -ms-flexbox;
        -ms-flex-direction: column;
      }
      
        .imgHeader{
            width: 200px;
            margin-right: 60px;
        }
        
        .img{
            width:150px;
        }
    }

@media (max-width: 426px){
    .f-container{
        height: 100vh; /* for shorter content */
        display: flex;
        flex-direction: column;
          
        /*ie10*/
        display: -ms-flexbox;
        -ms-flex-direction: column;
      }
      
        .imgHeader{
            width: 0;
        }
        
        .img{
            width:0;
        }
        .timer{
            display:inline-flex;
            font-family: 'block';
            color: #fff;
            background-color: #000;
            font-size: 34px;
            line-height: 1;
            margin-top:40px;
        } 
        
    }
    
    
    .footer h2{
        padding-top: 30px;
        font-family: 'block';
    }
    .footer a{
        font-family: 'BebasNeue';   
      }
    .footer a:link {
        color: rgb(50, 50, 50);
        text-decoration: none;
      }
      
      /* visited link */
    .footer a:visited {
        color: black;
        text-decoration: none;
      }
      
      /* mouse over link */
    .footer a:hover {
        color: maroon;
        font-weight: bold;
        text-decoration: none;
      }
      
      /* selected link */
    .footer  a:active {
        color: black;
        text-decoration: none;
      }

      .getqrcode{
        text-align: center;
        transform: scale(1);
        animation: pulse-black 2s infinite;
      }
    
      @keyframes pulse-black {
        0% {
            transform: scale(0.95);
        }
        
        70% {
            transform: scale(1);
        }
        
        100% {
            transform: scale(0.95);
        }
    }
    